body {
  background: #817d7d;
  font-family: "Roboto", sans-serif !important;
}
.brand {
  font-size: 32px;
  font-weight: 600;
  padding: 0px 30px;
  margin: 0px;
}
a:hover {
  cursor: pointer;
  color: aquamarine;
}
a {
  color: aquamarine;
}
.nav-tab,
.nav-link {
  font-size: 20px;
  font-weight: 400;
}
.content_style h5 {
  font: caption;
  margin-right: 20px;
}
.content_style p {
  font-size: 17px;
}
.content_style a {
  font-size: 17px;
}
.image_style {
  justify-content: center;
  align-items: center;
}
.content_div {
  padding: 40px;
  background-color: #3c3c3c;
  color: #f4f5f8;
}
.card-title {
  font: caption;
}
.summ {
  font-size: 18px;
  font-weight: 500;
  padding-top: 20px;
}
.character_card {
  display: flex;
  flex-direction: column;
  margin: 5px;
  background-color: #3c3c3c;
  color: #f4f5f8;
}

.person {
  font-size: 16px;
  color: aquamarine;
}
.nav-tabs > .nav-link:hover {
  color: #f4f5f8;
}
.nav-tabs {
  border-bottom: 1px solid #3c3c3c !important;
}
.nav-link {
  color: #f4f5f8;
}
.nav-link.active {
  color: #495057 !important;
  background-color: unset !important;
  border-color: #2b2c2d #2b2c2d #817d7d !important;
}
@media (max-width: 767px) {
  .content_div {
    margin: 0px 10px 0px 10px;
    width: 94vw;
  }
}
